// common mixins - start

@mixin mkdfBckImageStyle() {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}

@mixin mkdfImageOverlayHoverStyle($with-hover: true) {
    @if ($with-hover) {
        &:hover {
            &:after {
                opacity: 1;
            }
        }

        &:after {
            @include mkdfAbsoluteHolderLayout();

            content: '';
            background-color: rgba($default-heading-color, .4);
            opacity: 0;

            @include mkdfTransition(opacity .2s ease-in-out);
        }

    } @else {
        @include mkdfAbsoluteHolderLayout();

        content: '';
        background-color: rgba($default-heading-color, .4);
        opacity: 0;

        @include mkdfTransition(opacity .2s ease-in-out);
    }
}

@mixin mkdfButtonDefaultStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    width: auto;
    margin: 0;
    font-family: $default-heading-font;
    font-size: 17px;
    line-height: 1;
    font-weight: 700;
    text-transform: none;
    outline: none;
    box-sizing: border-box;
    border-radius: $default-border-radius;
    letter-spacing: -0.02em;

    @include mkdfTransition(color .2s ease-in-out, background-color .2s ease-in-out, border-color .2s ease-in-out);
}

@mixin mkdfButtonSize($size: medium) {
    @if ($size=='shop') {
        padding: 13px 34px !important;
    } @else if ($size == 'small') {
        padding: 11px 22px;
    } @else if ($size == 'medium') {
        padding: 14px 26px;
    } @else if ($size == 'large') {
        padding: 19px 30px;
    } @else if ($size=='huge') {
        padding: 24px 34px;
    } @else if ($size == 'huge-wide') {
        display: block;
        padding: 24px 34px;
        text-align: center;
        width: 100%;

        .mkdf-btn-text {
            display: inline-block;
        }
    }
}

@mixin mkdfButtonTransparentColor() {
    color: $default-text-color;
    background-color: transparent;
}

@mixin mkdfButtonSolidColor($important: null) {
    color: #fff $important;
    background-color: $default-heading-color $important;
    border: 1px solid transparent $important;
}

@mixin mkdfButtonSolidHoverColor($important: null) {
    color: #fff $important;
    background-color: $first-main-color $important;
}

@mixin mkdfButtonOutlineColor($important: null) {
    color: $first-main-color $important;
    background-color: transparent $important;
    border: 1px solid $first-main-color $important;
}

@mixin mkdfButtonOutlineHoverColor($important: null) {
    color: #fff $important;
    background-color: $first-main-color $important;
    border-color: $first-main-color $important;
}

@mixin mkdfPlaceholder {
    &::-webkit-input-placeholder {
        @content
    }

    &:-moz-placeholder {
        @content;

        opacity: 1;
    }

    &::-moz-placeholder {
        @content;

        opacity: 1;
    }

    &:-ms-input-placeholder {
        @content
    }
}

// common mixins - end

