// mixins styles - start

@mixin mkdfInputStyle($size:small) {
    @if ($size=='small') {
        background: none;
        font-size: 15px;
        line-height: calc(#{$small-input-height} - (#{$small-input-vertical-padding} * 2));
        margin: 0 0 $small-input-margin;
        padding: $small-input-vertical-padding $small-input-horizontal-padding;
    } @else if ($size=='large') {
        //background: url(../img/theme-pattern.png);
        //background-color: #ffffff;
        font-size: 21px;
        line-height: calc(#{$large-input-height} - (#{$large-input-vertical-padding} * 2));
        margin: 0 0 $large-input-margin;
        padding: $large-input-vertical-padding $large-input-horizontal-padding;
    }

    background-color: $additional-background-color;
    border: none;
    border-radius: 0;
    box-sizing: border-box;
    color: $default-heading-color;
    cursor: unset;
    font-family: $default-text-font;
    font-weight: inherit;
    outline: 0;
    position: relative;
    width: 100%;

    -webkit-appearance: none;

    @include mkdfTransition(border-color 0.2s ease-in-out);

    &:focus {
        color: $default-heading-color;
        border-color: $default-text-color;
    }

    @include mkdfPlaceholder {
        color: inherit;
    }
}

// mixins styles - end

