// heading mixins - start

@mixin mkdfDefaultHeadingStyle() {
    @include mkdfHeadingStyle();

    margin: 0;

    &:not([class*="mkdf"]) {
        margin: 25px 0;
    }

    a {
        @include mkdfTypographyLayout();

        &:hover {
            color: $first-main-color;
        }
    }
}

@mixin mkdfHeadingStyle($with-heading: null) {
    color: $default-heading-color;

    @if ($with-heading == 'h1') {
        @include mkdfH1();
    } @else if ($with-heading == 'h2') {
        @include mkdfH2();
    } @else if ($with-heading == 'h3') {
        @include mkdfH3();
    } @else if ($with-heading == 'h4') {
        @include mkdfH4();
    } @else if ($with-heading == 'h5') {
        @include mkdfH5();
    } @else if ($with-heading == 'h6') {
        @include mkdfH6();
    }
}

@mixin mkdfH1() {
    font-size: 65px;
    line-height: 1.1em;
    font-family: $default-text-font;
    font-weight: 400;

}

@mixin mkdfH2($important:null) {
    font-size: 34px $important;
    line-height: 1.176em $important;
    font-family: $default-text-font $important;
    font-weight: 400 $important;
}

@mixin mkdfH3() {
    font-size: 26px;
    line-height: 1.3em;
    font-weight: 400;
}

@mixin mkdfH4() {
    font-size: 20px;
    line-height: 1.2em;
    font-family: $default-heading-font;
    font-weight: 700;
}

@mixin mkdfH5() {
    font-size: 17px;
    line-height: 1.235em;
    font-family: $default-heading-font;
    font-weight: 700;
}

@mixin mkdfH6() {
    font-size: 16px;
    line-height: 1.3em;
    font-family: $default-text-font;
    font-weight: 400;
}

// heading mixins - end

