// pagination mixins - begin

@mixin mkdfStandardPaginationStyle($list_type: null) {
    $height: 108px;
    $arrow-offset: 38px;
    $separator-offset: 59px;

    @include mkdfRelativeHolderLayout(top);

    margin: 100px 0 0;
    clear: both;
    background: $additional-background-color;
    height: $height;
    position: relative;

    ul {
        @include mkdfRelativeHolderLayout(top);

        padding: 0;
        margin: 0;
        list-style: none;
        text-align: center;

        li {
            //position: relative;
            display: inline-block;
            vertical-align: top;
            margin: 0 22px;

            a {
                @include mkdfHeadingStyle(h4);

                font-size: 17px;
                line-height: $height;
                position: relative;
                display: inline-block;
                vertical-align: top;
                margin: 0;
                padding: 0;
                z-index: 1;

                &:hover {
                    color: $first-main-color;
                }
            }

            &.mkdf-pag-number {
                @include phone-landscape() {
                    display: none;
                }
            }

            .mkdf-active-dots {
                //opacity: 0;
                //line-height: 108px;
                //position: absolute;
                //top: 50%;
                //right: 50%;
                //z-index: 0;

                //@include mkdfTransform(translateY(-50%) translateX(50%));

                display: none;
            }

            &.mkdf-pag-active {
                @include phone-landscape() {
                    display: inline-block;
                }

                a {
                    color: $first-main-color;
                }

                //.mkdf-active-dots {
                //    opacity: 1;
                //}
            }

            &.mkdf-pag-prev,
            &.mkdf-pag-next {
                margin: 0;

                a {
                    //font-size: 24px;

                    span {
                        display: block;
                        line-height: inherit;

                        &:before {
                            content: '';
                            display: block;
                            line-height: inherit;
                            width: 1px;
                            height: 55px;
                            background-color: #dbdbdb;
                            position: absolute;
                            top: 50%;

                            @include mkdfTransform(translateY(-50%));

                            @include phone-landscape() {
                                display: none;
                            }
                        }
                    }

                    &:hover {
                        path {
                            fill: $first-main-color;
                        }
                    }
                }
            }

            &.mkdf-pag-prev {
                position: absolute;
                left: $arrow-offset;

                @include phone-landscape() {
                    left: 20px;
                }

                //@media only screen and (max-width: 400px) {
                //    left: 5px;
                //}

                a span:before {
                    left: 59px;
                }

                //> span {
                //    width: 1px;
                //    height: 55px;
                //    background-color: #dbdbdb;
                //    display: inline-block;
                //    position: absolute;
                //    left: 59px;
                //    top: 50%;
                //
                //    @include mkdfTransform(translateY(-50%));
                //}
            }

            &.mkdf-pag-next {
                position: absolute;
                left: auto;
                right: $arrow-offset;

                @include phone-landscape() {
                    right: 20px;
                }

                //@media only screen and (max-width: 400px) {
                //    right: 5px;
                //}

                a span:before {
                    right: 59px;
                }

                //> span {
                //    width: 1px;
                //    height: 55px;
                //    background-color: #dbdbdb;
                //    display: inline-block;
                //    position: absolute;
                //    right: 59px;
                //    top: 50%;
                //
                //    @include mkdfTransform(translateY(-50%));
                //}
            }

            @if ($list_type == 'shortcode') {
                &.mkdf-pag-prev {
                    a {
                        opacity: 0;
                    }
                }

                &.mkdf-pag-next {
                    a {
                        opacity: 1;
                    }
                }

            } @else if ($list_type == 'shop') {
                span {
                    //position: relative;
                    //display: inline-block;
                    //vertical-align: top;
                    //margin: 0;
                    //padding: 0;
                    //color: $first-main-color;

                    @include mkdfHeadingStyle(h4);

                    font-size: 17px;
                    line-height: $height;
                    position: relative;
                    display: inline-block;
                    vertical-align: top;
                    margin: 0;
                    padding: 0;
                    z-index: 1;
                    color: $first-main-color;
                }

                .page-numbers {
                    &:not(.prev):not(.next):not(.current) {
                        @include phone-landscape() {
                            display: none;
                        }
                    }

                    @include phone-landscape() {
                        &.prev {
                            left: 20px;
                        }

                        &.next {
                            right: 20px;
                        }

                        &.current {
                            left: 50%;
                            top: 0;
                            position: absolute;

                            @include mkdfTransform(translateX(-50%));
                        }
                    }
                }

                a {
                    &.next,
                    &.prev {
                        font-size: 0;
                        line-height: 0;
                        position: absolute;
                        top: 0;
                        width: 26px; // same as svg width, sort of hack

                        &:before {
                            background-repeat: no-repeat;
                            background-position: center;
                            content: '';
                            display: block;
                            height: $height;
                            width: 26px;
                            line-height: $height;

                            //height: 40px;
                            //font-family: 'ElegantIcons'; // same icon pack as in our templates for pagination
                            //font-size: 24px;
                            //line-height: 26px;
                            //-webkit-font-smoothing: antialiased;
                            //-moz-osx-font-smoothing: grayscale;
                        }

                        &:after {
                            content: '';
                            display: block;
                            line-height: inherit;
                            width: 1px;
                            height: 55px;
                            background-color: #dbdbdb;
                            position: absolute;
                            top: 50%;

                            @include mkdfTransform(translateY(-50%));

                            @include phone-landscape() {
                                display: none;
                            }
                        }
                    }

                    &.prev {
                        left: $arrow-offset;

                        &:before {
                            background-image: url(../img/arrow-left.svg);
                        }

                        &:after {
                            left: $separator-offset;
                        }
                    }

                    &.next {
                        right: $arrow-offset;

                        &:before {
                            background-image: url(../img/arrow-right.svg);
                        }

                        &:after {
                            right: $separator-offset;
                        }
                    }
                }
            }
        }
    }
}

// pagination mixins - end

